.footer_distributed {
  background-color: #292c2f;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  font: bold 16px sans-serif;
  padding: 55px 50px;
}

.footer_distributed .footer_left,
.footer_distributed .footer_center,
.footer_distributed .footer_right {
  display: inline-block;
  vertical-align: top;
}

.footer_distributed .footer_left {
  width: 40%;
}
h3 {
  color: #ffffff;
  font: normal 40px "Cookie", cursive;
  margin: 0;
}
h3 span {
  color: #ed3330;
}
.footer_distributed h3 {
  color: #ffffff;
  font: normal 36px "Cookie", cursive;
  margin: 0;
}

.footer_distributed h3 span {
  color: #ed3330;
}

.footer_distributed .footer_links {
  color: #ffffff;
  margin: 20px 0 12px;
  padding: 0;
}

.footer_distributed .footer_links a {
  display: inline-block;
  line-height: 1.8;
  text-decoration: none;
  color: inherit;
}

.footer_distributed .footer_company_name {
  color: #8f9296;
  font-size: 14px;
  font-weight: normal;
  margin: 0;
}

.footer_distributed .footer_center {
  width: 35%;
}

.footer_distributed .footer_center i {
  background-color: #33383b;
  color: #ffffff;
  font-size: 25px;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  text-align: center;
  line-height: 42px;
  margin: 10px 15px;
  vertical-align: middle;
}

.footer_distributed .footer_center i.fa-envelope {
  font-size: 17px;
  line-height: 38px;
}

.footer_distributed .footer_center p {
  display: inline-block;
  color: #ffffff;
  vertical-align: middle;
  margin: 0;
}

.footer_distributed .footer_center p span {
  display: block;
  font-weight: normal;
  font-size: 14px;
  line-height: 2;
}

.footer_distributed .footer_center p a {
  color: #ed3330;
  text-decoration: none;
}

.footer_distributed .footer_right {
  width: 20%;
}

.footer_distributed .footer_company_about {
  line-height: 20px;
  color: #92999f;
  font-size: 13px;
  font-weight: normal;
  margin: 0;
}

.footer_distributed .footer_company_about span {
  display: block;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px;
}

.footer_distributed .footer_icons {
  margin-top: 25px;
}

.footer_distributed .footer_icons a {
  display: inline-block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  background-color: #33383b;
  border-radius: 2px;

  font-size: 20px;
  color: #ffffff;
  text-align: center;
  line-height: 35px;

  margin-right: 3px;
  margin-bottom: 5px;
}

@media (max-width: 880px) {
  .footer_distributed {
    font: bold 14px sans-serif;
  }

  .footer_distributed .footer_left,
  .footer_distributed .footer_center,
  .footer_distributed .footer_right {
    display: block;
    width: 100%;
    margin-bottom: 40px;
    text-align: center;
  }

  .footer_distributed .footer_center i {
    margin-left: 0;
  }
  .main {
    line-height: normal;
    font-size: auto;
  }
}
